import Vue from 'vue'
import Router from 'vue-router'
import { Dialog } from 'vant';
import HelloWorld from '@/components/HelloWorld'
import Components from "@/components";
import SlideVerify from 'vue-monoplasty-slide-verify';
import Home from '@/pages/home';
import Mall from '@/pages/mall';
import Cart from '@/pages/mall/cart';
import Order from '@/pages/mall/order';
import Detail from '@/pages/mall/detail';
import Mine from '@/pages/mine';
import Register from '@/pages/user/register';
import Login from '@/pages/user/login';

Vue.use(Router);
Vue.use(Dialog);
//Components文件用来维护公用组件
Vue.use(Components);
Vue.use(SlideVerify);
export default new Router({
  // mode:"history",
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/mall',
      name: 'mall',
      component: Mall
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/detail/:id',
      name: 'detail',
      component: Detail
    },
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },{
      path: '/login',
      name: 'login',
      component: Login
    },
  ]
})



