<template>
  <footer class="footer">
    <router-view />
    <van-tabbar v-model="active" route>
      <van-tabbar-item replace to="/" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item replace to="/mall" icon="shop-o">商城</van-tabbar-item>
      <van-tabbar-item replace to="/cart" icon="cart-o">购物车</van-tabbar-item>
      <van-tabbar-item replace to="/mine" icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
    <!-- <slot></slot> -->
  </footer>
</template>

<script>
export default {
  name: 'Footer',
  data () {
    return {
      msg: 'i\'m footer',
      active:this.$store.state.tab
    }
  },
  methods:{
    change(active){
      switch(active){
        case "home":
          this.$store.dispatch("changeTab","home")
          this.$router.push("/");
          break;
        case "mall":
          this.$store.dispatch("changeTab","mall")
          this.$router.push("/mall");
          break;
        case "cart":
          this.$store.dispatch("changeTab","cart")
          this.$router.push("/cart");
          break;
        case "mine":
          this.$store.dispatch("changeTab","mine")
          this.$router.push("/mine");
          break;
      }
    }
  }
  // components:{ TabPane }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .footer{
    // height:50px;
    // border-top: 1px solid #ededed;
    box-shadow: 0 0 0.05rem 0 #c3c0c0;
  }
</style>



