<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'index'
}
</script>

<style lang="less">
@import "./assets/CSS/reset.less";
@import "./assets/CSS/common.less";
.flexBox{
  display: flex;
  display: -webkit-flex;
}
.flexBoxColumns{
  display: flex;
  display: -webkit-flex;
  flex-direction:column;
}
ul,li{
  list-style: none;
}
a{
  text-decoration: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // text-align: center;
  color: #2c3e50;
  width:100%;
  height:100%;
  max-width:750px;
  margin:0 auto;
  .wrap{
    height: 100%;
    width:100%;
    .flexBoxColumns();
  }
  .van-tabbar--fixed{
    position: initial;
  }
}
</style>



