<template>
  <div class="wrap">
    <Header :left="true" title="商品详情" :arrow="true" />
    <content class="content">
      <div class="detail">

      </div>
      <div class="goodAction">
        <van-goods-action>
          <van-goods-action-icon @click="go('/')" icon="home-o" text="首页" />
          <van-goods-action-icon icon="cart-o" text="购物车" />
          <van-goods-action-button type="warning" text="加入购物车" />
          <van-goods-action-button type="danger" text="立即购买" />
        </van-goods-action>
      </div>
    </content>
  </div>
</template>

<script>
  export default {
    name: 'Mall',
    data () {
      return {
        msg:"详情页"
      }
    },
    methods:{
      go(path){
        this.$router.push(path);
      }
    }
  }
</script>

<style lang="less" scoped>
  .content{
    flex: 1;
    background: #f5f5f5;
    overflow:auto;
    display: flex;
    flex-direction:column;
    .detail{
      flex: 1;
      overflow: auto;
    }
    .goodAction{
      height: 50px;
    }
  }
</style>



