<template>
  <div class="wrap">
    <Header :left="true" :arrow="true" title="登录" />
    <content class="content">
      <van-cell-group>
        <van-field
          v-model="user.username"
          placeholder="请输入用户名"
        />
      </van-cell-group>
      <van-cell-group>
        <van-field
          type="password"
          v-model="user.psd"
          placeholder="请输入密码"
        />
      </van-cell-group>
      <van-button type="info" class="login" @click="login" :loading="logining" :disabled="logining">登录</van-button>
      <div class="tip">
        <router-link to="/register">忘记密码？</router-link>
        <router-link to="/register" class="toRe">注册</router-link>
      </div>
    </content>
    <!-- <Footer /> -->
  </div>
</template>

<script>
import request from "@/utils/request"
import { Dialog } from 'vant';
import Cookies from "js-cookie";
export default {
  name: 'Login',
  data () {
    return {
      msg: "登录页面",
      logining:false,
      user:{
        username:"",
        psd:""
      }
    }
  },
  methods:{
    login(){
      const { username, psd } = this.user;
      console.log(username, psd);
      let param = {username, psd};
      this.logining = true;
      request("/login",param).then(res=>{
        console.log(res);
        this.logining = false;
        if(res.data.success===1){
           Dialog({
            title: '提示',
            message: '登录成功!'
          }).then(()=>{
            Cookies.set("username",username);
            this.$router.go(-1)
          });
        }else{
          Dialog({
            title: '提示',
            message: '登录失败!'
          });
        }
      }).catch(err=>{
        this.logining = false;
        console.log(err);
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    background: #f5f5f5;
    padding: 0.4rem;
    overflow:auto;
    .login{
      margin-top:0.4rem;
      width:100%;
    }
    .tip{
      padding-top:0.2rem;
      text-align: left;
      a{
        text-decoration:underline;
        cursor: pointer;
      }
      .toRe{
        float:right;
      }
    }
  }
</style>



