import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const goods = [
  {
    id:1,
    type:"标准果",
    num:1000,
    price:78.00,
    desc:"10斤标准果,果径65-75mm",
    title:"10斤标准果",
    img:"https://img.yzcdn.cn/vant/cat.jpeg",
    ori_price:98.00
  },
  {
    id:2,
    type:"精品果",
    num:1000,
    price:98.00,
    desc:"10斤精品果,果径75-85mm",
    title:"10斤精品果",
    img:"https://img.yzcdn.cn/vant/cat.jpeg",
    ori_price:128.00
  },
  {
    id:3,
    type:"标准果",
    num:1000,
    price:138.00,
    desc:"20斤标准果,果径65-75mm",
    title:"20斤标准果",
    img:"https://img.yzcdn.cn/vant/cat.jpeg",
    ori_price:158.00
  },
  {
    id:4,
    type:"精品果",
    num:1000,
    price:168.00,
    desc:"20斤精品果,果径75-85mm",
    title:"20斤精品果",
    img:"https://img.yzcdn.cn/vant/cat.jpeg",
    ori_price:198.00
  }
];
const filter_option1 = [
  {text:"全部商品",value:0},
  {text:"标准果",value:1},
  {text:"精品果",value:2},
]
const filter_option2 = [
  {text:"默认排序",value:0},
  {text:"价格升序",value:1},
  {text:"价格降序",value:2},
]
const filter_option = [filter_option1,filter_option2];
const banner = [
  {
    // src:"https://img.yzcdn.cn/vant/cat.jpeg",
  },
  {
    // src:"https://img.yzcdn.cn/vant/cat.jpeg",
  },
  {
    // src:"https://img.yzcdn.cn/vant/cat.jpeg",
  },
  {
    // src:"https://img.yzcdn.cn/vant/cat.jpeg",
  }
];
const store = new Vuex.Store({
  state:{
    tab:"home",
    goods,
    banner,
    filter_option,
    filter_val:[0,0]
  },
  mutations:{
    saveTab(state,payload){
      state.tab=payload;
    }
  },
  actions:{
    changeTab(content,payload){
      content.commit("saveTab",payload)
    }
  }
});
export default store;


