<template>
  <div class="wrap">
    <Header :left="true" title="个人中心" />
    <content class="content">
      <van-row class="ad">
        <van-image
          width="100%"
          height="3rem"
          src="https://img.yzcdn.cn/vant/cat.jpeg"
        />
      </van-row>
      <van-row class="user" type="flex" justify="center">
        <van-col span="10" class="avatar" v-if="isLogin">
          <van-image
            round
            width="2rem"
            height="2rem"
            :src="user.img"
          />
        </van-col>
        <van-col span="10" class="no_login" v-if="!isLogin">
          <router-link to="/login">
            <div class="to_login">点击登录</div>
          </router-link>
        </van-col>
      </van-row>
      <div class="user_info" v-if="isLogin">
        <van-cell-group>
          <van-cell title="用户名" value="周文星" />
        </van-cell-group>
        <van-cell-group>
          <van-cell title="性别" value="男" />
        </van-cell-group>
        <van-cell-group>
          <van-cell title="我的订单" value="查看全部订单" is-link @click="go_path('order')" />
        </van-cell-group>
        <van-cell-group>
          <van-cell title="购物车" value="查看购物车" is-link @click="go_path('cart')" />
        </van-cell-group>
        <van-row class="align_center login_out">
          <van-button type="info" size="small" @click="login_out">退出登录</van-button>
        </van-row>
      </div>
    </content>
    <Footer />
  </div>
</template>

<script>
import Cookies from "js-cookie";
export default {
  name: 'Mine',
  data () {
    return {
      msg: '个人中心内容',
      isLogin:false,
      user:{

      }
    }
  },
  // components:{ Header, Footer },
  methods:{
    checkLogin(){//检查是否登录
      const username=Cookies.get('username');
      if(!username){
        this.isLogin = false;
      }else{
        this.isLogin = true;
      }
    },
    go_path(path){//点击我的订单
      this.$store.dispatch("changeTab",path)
      this.$router.push("/"+path);
    },
    login_out(){//退出登录
      Cookies.remove("username");
      this.checkLogin();
    }
  },
  created:function () {
    this.checkLogin();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    // padding:0.4rem;
    overflow: auto;
    .ad{
      background:#fff;
    }
    .user{
      background:#fff;
      padding: 0.2rem;
      // margin:0.4rem;
      .to_login{
        width:3rem;
        height: 3rem;
        text-align: center;
        line-height: 3rem;
        border-radius: 50%;
        background: #f5f5f5;
        font-size: 0.28rem;
        box-shadow: 0 0 0.05rem 0 #c3c0c0;
      }
      .avatar{
        text-align:center;
      }
      .user_col{
        height:1rem;
      }
      .no_login{
        text-align:center;
        line-height:2rem;
        color: #888484;
      }
    }
    .user_info{
      padding:0 0.4rem;
      .login_out{
        padding:0.2rem 0;
        .van-button{
          width:100%;
          letter-spacing: 0.1rem;
        }
      }
    }
  }
</style>



