export const pattern = {
  username:{
    reg:/^[a-z|A-Z|\d]{2,18}$/,
    errMsg:"用户名只能包含数字和字母，长度2-18位"
  },
  psd:{
    reg:/^[a-z|A-Z|\d]{8,18}$/,
    errMsg:"用户名只能包含数字和字母，长度8-18位"
  },
  email:{
    reg:/^[a-z|A-Z|\d]{6,18}@[a-z|A-Z|\d]{1,9}\.[a-z|A-Z|\d]{1,9}$/,
    errMsg:"邮箱格式错误"
  },
  code:{
    reg:/^[\d]{4}$/,
    errMsg:"验证码是4位数字"
  }
}


