<template>
  <div class="wrap">
    <Header :left="true" title="商城" />
    <content class="content">
      <div class="filter">
        <Goods />
      </div>
      <div class="list">
        <van-card
          :num="calnum(item.num)"
          :price="item.price"
          :desc="item.desc"
          :title="item.title"
          :thumb="item.img"
          :origin-price="item.ori_price"
          v-for="item in goods"
          :key="item.id"
          @click-thumb="goDetail(item)"
        >
          <div slot="tags">
            <van-tag plain type="danger">{{item.type}}</van-tag>
          </div>
          <div slot="footer">
            <van-icon name="add-o" class="icon" />
            <van-icon name="cart-circle-o" class="icon" />
          </div>
        </van-card>
      </div>
    </content>
    <Footer />
  </div>
</template>

<script>
import Goods from "./list";
export default {
  name: 'Mall',
  data () {
    return {
      msg: '商城内容',
    }
  },
  components:{ Goods },
  methods: {
    calnum(num){
      return num>999?"999+":num;
    },
    goDetail(item){
      this.$router.push("/detail/"+item.id)
    }
  },
  computed:{
    goods(){
      const { goods, filter_option, filter_val } = this.$store.state;
      const value = filter_val.map((val,i)=>{
        return filter_option[i].filter(d=>d.value===val)[0].text;
      })
      let good=JSON.parse(JSON.stringify(goods));
      if(filter_val[0])
        good = good.filter(d=>d.type===value[0])
      if(filter_val[1]){
        if(filter_val[1]===1)
          good = good.sort((a,b)=>a.price-b.price);
        if(filter_val[1]===2)
          good = good.sort((a,b)=>b.price-a.price);
      }
      return good;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    background: #f5f5f5;
    overflow:auto;
    display: flex;
    flex-direction:column;
    .filter{
      background:orange;
      box-shadow: 0 0 0.05rem 0 #c3c0c0;
      .van-dropdown-menu{
        border-top-left-radius:10px;
        border-top-right-radius:10px;
      }
      .van-hairline--top-bottom::after{
        display:none;
      }
    }
    .list{
      flex: 1;
      overflow: auto;
      margin-bottom:0.1rem;
      .van-card{
        background:#fff;
        margin-top:0;
        .van-card__thumb{
          background: #c3c0c0;
        }
        .icon{
          color: #e60d0d;
        }
      }
    }
  }
</style>



