<template>
  <header class="header">
    <van-nav-bar
      :title="title"
      class="header_nav"
      :left-arrow="arrow"
      @click-left="onClickLeft"
    />
  </header>
</template>

<script>
export default {
  name: 'Header',
  props:["title","left","arrow"],
  data () {
    return {
      msg: 'i\'m header'
    }
  },
  methods:{
    onClickLeft(){
      this.$router.go(-1);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .header{
    // border-bottom: 1px solid #ededed;
    background: #736363;
    color: #fff;
    .header_nav{
      background: orange;
      .van-nav-bar__left{
        color: #fff;
        .van-icon-arrow-left{
          color: #fff;
        }
      }
      .van-nav-bar__title{
        color:#fff;
      }
      &::after{
        display:none;
      }
    }
  }
</style>



