<template>
  <div class="wrap">
    <Header title="首页" />
    <content class="content">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(img, i) in banner" :key="i">
          <van-image
            width="100%"
            height="3rem"
            :src="img.src"
          />
        </van-swipe-item>
      </van-swipe>
      <van-notice-bar left-icon="volume-o">
        舌尖美味,赣南脐橙将于11月份采摘,基地直供请认准www.yaoxianguo.com【要鲜果】!
      </van-notice-bar>
      <div class="wish">
        <div class="logo">橙</div>
        <div class="txt">
          <p>最新鲜的橙子不在超市,而是在你下单的时候它还在树上!</p>
          <p>我什么都不能给你，除了最新鲜美味的赣南脐橙！【要鲜果】</p>
        </div>
      </div>
      <div class="rec">
        <van-card
          v-for="(item,i) in goods"
          :key="i"
          :num="calnum(item.num)"
          :price="item.price"
          :desc="item.desc"
          :title="item.title"
          :thumb="item.img"
          :origin-price="item.ori_price"
          @click-thumb="goDetail(item)"
        >
          <div slot="tags">
            <van-tag plain type="danger">{{item.type}}</van-tag>
          </div>
          <div slot="footer">
            <van-icon name="cart-circle-o" class="shop_car" />
          </div>
        </van-card>
      </div>
    </content>
    <Footer />
  </div>
</template>

<script>
import TabPane from "@/components/tabPane";
export default {
  name: 'Home',
  data () {
    const { banner, goods } = this.$store.state;
    return {
      banner,
      goods
    }
  },
  methods:{
    calnum(num){
      return num>999?"999+":num;
    },
    goDetail(item){
      this.$router.push("/detail/"+item.id)
    }
  },
  components:{ TabPane }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    -webkit-flex:1;
    background: #f5f5f5;
    overflow:auto;
    .banner{
      width:100%;
      height:3rem;
      background: #fff;
    }
    .wish{
      background: #fff;
      padding: 0.1rem 0.4rem;
      .logo{
        border-radius: 50%;
        width:1rem;
        height: 1rem;
        font-size: 0.4rem;
        margin: 0 auto;
        background: orange;
        color: #fff;
        text-align: center;
        line-height: 1rem;
      }
      .txt{
        padding: 0.2rem 0.4rem 0;
        p{
          text-align: center;
          font-size: 0.26rem;
          line-height: 0.4rem;
        }
      }
    }
    .rec{
      padding:0.2rem 0.4rem;
      .van-card{
        background:orange;
        .van-card__thumb{
          background: #c3c0c0;
        }
        .van-card__content{
          color: #fff;
          .van-card__desc.van-ellipsis{
            color: #57595f;
          }
        }
        .shop_car{
          color: #e60d0d;
        }
      }
    }
  }
</style>



