import Vue from 'vue';
import { Button, Icon, Tabbar, TabbarItem, NavBar, Field, Tag, Cell, CellGroup, Image, Grid, GridItem, Swipe, SwipeItem, Card, NoticeBar, DropdownMenu, DropdownItem, GoodsAction, GoodsActionIcon, GoodsActionButton, Row, Col } from 'vant';
import Header from "@/components/header";
import Footer from "@/components/footer";

const install = ()=>{
  // Vue.component("Wrap",Wrap);
  Vue.component(Header.name,Header);
  Vue.component(Footer.name,Footer);
  Vue.component(Button.name,Button);
  Vue.component(Icon.name,Icon);
  Vue.component(Tabbar.name,Tabbar);
  Vue.component(TabbarItem.name,TabbarItem);
  Vue.component(NavBar.name,NavBar);
  Vue.component(Field.name,Field);
  Vue.component(Tag.name,Tag);
  Vue.component(Cell.name,Cell);
  Vue.component(CellGroup.name,CellGroup);
  Vue.component(Image.name,Image);
  Vue.component(Grid.name,Grid);
  Vue.component(GridItem.name,GridItem);
  Vue.component(Swipe.name,Swipe);
  Vue.component(SwipeItem.name,SwipeItem);
  Vue.component(Card.name,Card);
  Vue.component(NoticeBar.name,NoticeBar);
  Vue.component(DropdownMenu.name,DropdownMenu);
  Vue.component(GoodsAction.name,GoodsAction);
  Vue.component(DropdownItem.name,DropdownItem);
  Vue.component(GoodsActionIcon.name,GoodsActionIcon);
  Vue.component(GoodsActionButton.name,GoodsActionButton);
  Vue.component(Row.name,Row);
  Vue.component(Col.name,Col);
}
export default install;



