<template>
  <div class="wrap">
    <Header :left="true" :arrow="true" title="注册" />
    <content class="content">
      <van-cell-group>
        <van-field
          label="用户名"
          @input="onChange"
          v-model="regInfo.username"
          placeholder="请输入用户名"
          required
          border
        />
        <van-field
          label="密码"
          type="password"
          @input="onChange"
          v-model="regInfo.psd"
          placeholder="请输入密码"
          required
          border
        />
        <van-field
          label="邮箱"
          @input="onChange"
          v-model="regInfo.email"
          placeholder="输入邮箱以接收验证码"
          required
          border
        />
        <van-field
          label="验证码"
          @input="onChange"
          v-model="regInfo.code"
          maxlength="4"
          center
          clearable
          placeholder="请输入邮箱验证码"
          required
          border
        >
          <van-button
            :disabled="isGetcode"
            slot="button"
            size="small"
            type="primary"
            @click="showSlider"
          >
            {{regInfo.codeAble?"发送验证码":`${regInfo.code_time}秒`}}
          </van-button>
        </van-field>
      </van-cell-group>
      <van-button
        type="info"
        class="register"
        @click="register()"
        :disabled="isRe||registing"
        :loading="registing"
        >
        注册
      </van-button>
      <div class="tip">
        <router-link to="/login" class="toRe">已有账号？去登录</router-link>
      </div>
      <van-dialog
        v-model="regInfo.showSlider"
        :title="slider.text"
        show-cancel-button
        :show-confirm-button="false"
      >
        <slide-verify
          class="slider"
          v-if="regInfo.showSlider"
          :l="42"
          :r="10"
          :w="310"
          :h="155"
          @success="onSuccess"
          @fail="onFail"
          @refresh="onRefresh"
          :slider-text="slider.text"
        >
        </slide-verify>
      </van-dialog>
    </content>
    <!-- <Footer /> -->
  </div>
</template>

<script>
import { pattern } from "@/utils"
import { Dialog } from 'vant';
import request from "@/utils/request"
export default {
  name: 'Register',
  data () {
    return {
      regInfo:{
        username:"",
        usernameErrMsg:pattern.username.errMsg,
        psd:"",
        psdErrMsg:pattern.psd.errMsg,
        email:"",
        emailErrMsg:pattern.email.errMsg,
        code:"",
        codeAble:true,
        code_time:60,
        codeErrMsg:pattern.code.errMsg,
        isCheck:false,
        showSlider:false
      },
      registing:false,
      slider:{
        text:"右滑验证"
      }
    }
  },
  methods:{
    showSlider(){
      this.regInfo.showSlider = true;
    },
    getCode(){//获取邮箱验证码
      this.regInfo.codeAble = false;
      this.regInfo.code_time = 60;
      this.calCodeTime();
      const { username, psd, email, code } = this.regInfo;
      const param = {
        email
      }
      request("/get_code",param).then(res=>{
        const { data }=res;
        if(data.success===1){
          Dialog({
            title: '提示',
            message: '已发送验证码,请查收邮箱!'
          });
        }else{
          Dialog({
            title: '提示',
            message: '获取验证码失败!'
          });
        }
      }).catch(err=>{
        console.log(err);
        Dialog({
          title: '提示',
          message: '获取验证码失败!'
        });
      })
    },
    register(){//注册
      const { username, psd, email, code } = this.regInfo;
      const param = {
        username, psd, email, code
      }
      this.registing = true;
      request("/register",param).then(res=>{
        this.registing = false;
        const { data }=res;
        if(data.success===1){
          Dialog({
            title: '提示',
            message: '注册成功!'
          }).then(()=>{
            Cookies.set("username",username);
            this.$router.go(-1);
          });
        }else{
          Dialog({
            title: '提示',
            message: data.msg||'注册失败!'
          });
        }
      }).catch(err=>{
        this.registing = false;
        console.log(err);
        Dialog({
          title: '提示',
          message: data.msg||'注册失败!'
        });
      })
    },
    calCodeTime(){//验证码倒计时功能
      this.code_timer = setInterval(()=>{
        console.log(this.regInfo.code_time)
        this.regInfo.code_time--;
        if(this.regInfo.code_time<=0){
          clearInterval(this.code_timer);
          this.regInfo.codeAble = true;
          this.regInfo.code_time = 60;
        }
      },1000)
    },
    isChecked(){
      //
    },
    onSuccess(){
      this.regInfo.isCheck=true;
      this.regInfo.showSlider = false;
      this.getCode();
    },
    onFail(){
      this.regInfo.isCheck=false;
    },
    onRefresh(){
      this.regInfo.isCheck=false;
    },
    onChange(val){
      if(!val){
        // this.regInfo.isCheck=false;
      }
    },
  },
  computed:{
    isRe(){//判断是否可以点击注册按钮
      const { username, psd, email, code, isCheck } = this.regInfo;
      let flag = false;
      const usernameReg = pattern.username.reg;
      const psdReg = pattern.psd.reg;
      const emailReg = pattern.email.reg;
      const codeReg = pattern.code.reg;
      if(username&&psd&&email&&code&&usernameReg.test(username)&&psdReg.test(psd)&&emailReg.test(email)&&codeReg.test(code)){
        flag = true;
      }
      return !flag||!isCheck;
    },
    isGetcode(){//判断是否可以点击获取验证码
      const { username, psd, email, codeAble } = this.regInfo;
      let flag = false;
      const usernameReg = pattern.username.reg;
      const psdReg = pattern.psd.reg;
      const emailReg = pattern.email.reg;
      if(codeAble&&username&&psd&&email&&usernameReg.test(username)&&psdReg.test(psd)&&emailReg.test(email)){
        flag = true;
      }
      return !flag;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    background: #f5f5f5;
    padding: 0.4rem;
    overflow: auto;
    .register{
      margin-top:0.4rem;
      width:100%;
    }
    .slider{
      margin:0.4rem auto 0;
    }
    .tip{
      padding-top:0.2rem;
      text-align: left;
      a{
        text-decoration:underline;
        cursor: pointer;
      }
      .toRe{
        float:right;
      }
    }
  }
</style>



