<template>
  <li class="tabPane">
    <router-link :to="{path:url}">
      <div class="tab-bar">
        <p>
          <span class="icon iconfont">&#xe60c;</span>
          <!-- <span class="icon iconfont">{{icon}}</span> -->
        </p>
        <p>
          {{name}}
        </p>
      </div>
    </router-link>
  </li>
</template>

<script>
export default {
  name: 'Footer',
  props:["name","url","icon"],
  data () {
    return {
      msg: 'i\'m footer'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .tabPane{
    display: flex;
    height:100px;
    flex: 1;
    border-right:1px solid #ededed;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:nth-last-of-type(1){
      border-right: none;
    }
    a{
      display: block;
    }
  }
</style>



